import { useCallback } from "react"
import { useAtom } from "jotai"
import { atomWithStorage } from "jotai/utils"

const loginRouteAtom = atomWithStorage<string | undefined>("redirectAfterLogin", undefined)

const useLoginRouteAtom = () => {
  const [loginRoute, setLoginRoute] = useAtom(loginRouteAtom)

  const consumeLoginRoute = useCallback(() => {
    const storedLoginRoute = loginRoute
    if (storedLoginRoute !== undefined) {
      setLoginRoute(undefined)
    }

    return storedLoginRoute
  }, [loginRoute, setLoginRoute])

  return { setLoginRoute, consumeLoginRoute }
}

export { useLoginRouteAtom }
